<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-3">
      <button class="btn btn-primary" @click="openModal(true)">Create Product</button>
    </div>
    <table class="table table-hover mt-3">
      <thead>
        <tr>
          <th width="180">Category</th>
          <th>Title</th>
          <th class="text-right" width="120">
            Price
            <a href="#" class="text-dark" @click.prevent="sortByPrice">
              <i class="fa fa-fw fa-sort"></i>
            </a>
          </th>
          <th width="100">Enable/Disable</th>
          <th width="180">Edit</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in products" :key="key">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.price | currencyFilter}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">Enable</span>
            <span v-else class="text-warning">Disable</span>
          </td>
          <td>
            <button class="btn btn-outline-primary mr-2" @click="openModal(false,item)">Edit</button>
            <button class="btn btn-outline-danger" @click="openDeleteModal(item)">Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
    <paginationCom :pagination="pagination"></paginationCom>
    <!----Delete Modal ---->
    <div class="modal" id="deleteModal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Warning Message</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>You want to delete the item ?</p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">No</button>
            <button type="button" class="btn btn-primary" @click="deleteProduct">Yes</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image">輸入圖片網址</label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder="請輸入圖片連結"
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                    或 上傳圖片
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.imageUrl"
                  alt
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title">標題</label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder="請輸入標題"
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category">分類</label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder="請輸入分類"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">單位</label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder="請輸入單位"
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price">原價</label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder="請輸入原價"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">售價</label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder="請輸入售價"
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description">產品描述</label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder="請輸入產品描述"
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content">說明內容</label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder="請輸入產品說明內容"
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label class="form-check-label" for="is_enabled">是否啟用</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import paginationCom from "@/components/pagination.vue"
export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      isNew: false,
      deleteProductId: "",
      isLoading: false,
      status: {
        fileUploading: false
      },
      pagination: {},
      ascend: false,
    };
  },
  components: {
      paginationCom,
  },
  methods: {
    sortByPrice() {
        var tmpArr = [];
        var vm = this;
        
        tmpArr = vm.products.sort(function (a, b) {
                    if (vm.ascend == true) {
                      return a.price - b.price;
                    } else {
                      return b.price - a.price;
                    }
                })
        
        this.ascend = !this.ascend;
        //vm.products = tmpArr;
        vm.products = Object.assign([], tmpArr);
      
        console.log(tmpArr);
    },
    getProducts(page = 1) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/products?page=${page}`;
      console.log("Get products of page " + page);
      console.log(api);
      var vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log("response data:");
        console.log(response);
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        console.log(vm.products);
        vm.isLoading = false;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.isNew = isNew;
        this.tempProduct = {};
      } else {
        this.isNew = isNew;
        this.tempProduct = Object.assign({}, item);
      }
      $("#productModal").modal("show");
    },
    openDeleteModal(item) {
      this.deleteProductId = item.id;
      console.log(this.deleteProductId);
      $("#deleteModal").modal("show");
    },
    deleteProduct() {
      var vm = this;
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/product/${vm.deleteProductId}`;
      let httpMethod = "delete";
      console.log("delete product .....");
      console.log(api);
      this.$http[httpMethod](api).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          $("#deleteModal").modal("hide");
          vm.getProducts();
        } else {
          $("#deleteModal").modal("hide");
          vm.getProducts();
          console.log("Failure to delete product");
          vm.$bus.$emit("message:push", response.data.message, "danger");
        }
      });
      ///api/:api_path/admin/product/:product_id
    },
    updateProduct() {
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/product`;
      let httpMethod = "post";
      console.log("Update products .......");
      console.log(api);
      var vm = this;
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/product/${vm.tempProduct.id}`;
        httpMethod = "put";
        console.log("Edit products .......");
      } else {
        console.log("Update products .......");
      }
      console.log(api);
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          $("#productModal").modal("hide");
          vm.getProducts(vm.pagination.current_page);
        } else {
          $("#productModal").modal("hide");
          vm.getProducts(vm.pagination.current_page);
          console.log("Failure to add product");
          vm.$bus.$emit("message:push", response.data.message, "danger");
        }
      });
    },
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      vm.status.fileUploading = true;
      formData.append("file-to-upload", uploadedFile);
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/upload`;
      this.$http
        .post(api, formData, {
          headers: {
            "Content-type": "multipart/form-data"
          }
        })
        .then(response => {
          console.log(response.data);
          if (response.data.success) {
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
          } else {
            vm.$bus.$emit(
              "message:push",
              response.data.message.message,
              "danger"
            );
          }
          vm.status.fileUploading = false;
        });
    }
  },
  created() {
    console.log("here is product created");
    this.getProducts();
  },
  mounted() {
      this.$on('refreshProduct',this.getProducts)
  },
};
</script>